            <p >“<b>为更好的每一天-感恩有你</b>”活动旨在鼓励我们每个人能够在同事、主管或团队作出有价值的贡献时及时地传递“感谢”，肯定大家付出的努力，进而打造一支高绩效团队，为更好的每一天<b>携手前行，共同努力</b>。<br><br> </p>
             <p >活动期间，从公司高管的感恩时刻到各分支机构的感恩仪式再到员工“为更好的每一天”21天打卡活动，每个人均通过各种方式积极参与到了活动中。此外，共计<b>122</b>位，来自<b>57</b>个不同部门及分支机构的同事因其在<b>自身岗位上的突出表现</b>获得了来自公司高层的表彰并被授予了纪念版证书。</p>
